import React from 'react'
import { DASHBOARD_SIDEBAR_LINKS } from '../Consts/Navigation'
import { Link, useLocation } from 'react-router-dom'
import {FcBullish} from "react-icons/fc"
import classNames from 'classnames'
import { HiOutlineLogout } from 'react-icons/hi'
import Logout from '../main pages/Logout'

const linkClasses ='flex items-center gap-2 font-light px-3 no-underline py-2 hover:bg-neutral-700 hover:no-underline active:bg-neutral-600 rounded-sm text-base'

export default function Sidebar  ()  {
  return (
	<div className='bg-slate-900 w-60 p-3 flex flex-col text-white'>
		<div className='flex items-center gap-2 px-1 py-3 '>
			<FcBullish fontSize={24}/>
			<span className='text-neutral-100 text-lg'>Dashboard</span>
			</div>
		<div className='flex-1 py-8 flex flex-col gap-0.5'>
			{DASHBOARD_SIDEBAR_LINKS.map((item)=>(
				<SidebarLink key={item.key} item={item}/>
				
			))}
		</div>
		<div className='text-xl items-center flex  m-3 flex-row '>
               <HiOutlineLogout/><Logout/>
		</div>

		</div>
		
  )
}

function SidebarLink({item}){
const {pathname} = useLocation()


	return(
		<Link to={item.path} className={classNames(pathname === item.path ? 'bg-neutral-700 text-white' : 'text-blue-400',linkClasses)}>
<span className='text-xl'>{item.icon}</span>
{item.label}
		</Link>
	)

}